import { Paper, Typography } from '@mui/material'
import React from 'react'
import './style.scss'
function ServiceCard({service}) {
  return (
   <div className='Card'>
      <Paper elevation={3} className='Service-Card'>
          <div className="Card-front">
            <img src={service.img} alt="" />
            <Typography variant='h4' sx={{fontWeight:"600"}} color="warning">
              {service.name}
            </Typography>
          </div>
          <div className="Card-back">
            <Typography variant='h5' className='Card-Title' sx={{fontWeight:"600",marginBottom:"15px !important"}} gutterBottom>{service.name}</Typography>
            <Typography align='justify' className='Card-text' variant='h6'>
              {service.description}
            </Typography>
          </div>
      </Paper>
   </div>
  )
}

export default ServiceCard