import { Typography } from '@mui/material'
import React from 'react'
import './style.scss'
function BlogCard({news}) {
  return (
    <a href={news.url} className="BlogCard">
      <div className='BlogCard-img'>
            <img src={news.img} alt="" />
      </div>
      <div className="BlogCard-text">
        <Typography variant='h5' className='shownText'>
          {news.name}
        </Typography>
        <Typography variant='p' align='justify' component={"p"} className='hiddenText'>
          {
            news.description
          }
        </Typography>
      </div>
    </a>
  )
}

export default BlogCard