import { Container, Grid, Typography } from "@mui/material";
import React from "react";
import "./style.scss";
import Geoforce from "../../assets/images/Blog/geoforce.png";
import Engineer from "../../assets/images/Blog/enginner.png";
import Science from "../../assets/images/Blog/science.png";
import { BlogCard } from "../../components";
function Blog() {
  let blogNews = [
    {
      name: "GeForce RTX 3090 Ti Review: Fast and Dumb",
      img: Geoforce,
      description: `Nvidia launched the first RTX Ampere GPUs about 18 months ago, 
      including the flagship GeForce RTX 3090, a $1,500 graphics card that we said was a boring and 
      rather pointless release in our day-one review. At least to gamers, the RTX 3090 made little 
      sense since there was no need to have a massive 24GB VRAM buffer, 
      and despite Nvidia's best efforts, 8K gaming just isn't a thing.`,
      url: "https://www.techspot.com/review/2442-geforce-rtx-3090-ti/",
    },
    {
      name: "Scientists have sequenced full human genome for the first time",
      img: Science,
      description: `Why it matters: Scientists are confident the complete picture of the human genome will lead 
      to an even better understanding of human evolution and perhaps help pave the way for discoveries in the
       fields of neurodegenerative conditions, aging, heart disease and cancer.
       Such discoveries are still years away, but at least scientists now have a complete roadmap to work with.
       Scientists have finally finished mapping the human genome, a project that has collectively spanned more than two decades.`,
      url: "https://www.techspot.com/news/94025-scientists-have-sequenced-full-human-genome-first-time.html",
    },
    {
      name: "Engineer builds the first Android phone with a Lightning port",
      img: Engineer,
      description: `The Lightning interface is a proprietary data transfer and charging port exclusive to
       Apple devices since the iPhone 5. However, 
      that didn't stop a robotics engineer from adding a Lightning connector 
      to an Android phone in a 'just because I can' project.
      Late last year, a robotics engineer named Ken Pillonel (Kenny Pi)
       showed us the first iPhone with a working USB-C port.
       This year he decided to invert this idea and modded an Android phone with a Lightning port.`,
      url: "https://www.techspot.com/news/94020-engineer-mods-android-phone-create-first-one-featuring.html",
    },
  ];
  return (
    <div id="blog" className="Blog">
      <Container>
        <div>
          <Typography
            variant="h2"
            component="h2"
            className="Section-upper-Title"
            align="center"
            gutterBottom
          >
            Our Blog
          </Typography>
          <Typography
            variant="h2"
            component="h2"
            className="Section-bottom-Title"
            align="center"
            gutterBottom
          >
            Latest News From Our Blog
          </Typography>
        </div>

        <Grid container rowSpacing={4} columnSpacing={{xs:0,sm:2,md:3}}>
          {
            blogNews.map((news,index)=>(
              <Grid item  xs={12} sm={6} md={4} key={index}>
                    <BlogCard news={news}/>
              </Grid>
            ))
          }
        </Grid>
      </Container>
    </div>
  );
}

export default Blog;
