import React from 'react'
import Container from '@mui/material/Container';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import SegmentIcon from '@mui/icons-material/Segment';
import { Link} from 'react-scroll'
import Drawer from '@mui/material/Drawer';
import MuiAppBar from '@mui/material/AppBar';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import HomeIcon from '@mui/icons-material/Home';
import SettingsAccessibilityIcon from '@mui/icons-material/SettingsAccessibility';
import AssignmentTurnedInIcon from '@mui/icons-material/AssignmentTurnedIn';
import LayersIcon from '@mui/icons-material/Layers';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import AddReactionIcon from '@mui/icons-material/AddReaction';
import { styled, useTheme } from '@mui/material/styles';
import { useState } from 'react';
import './style.scss'
function Header() {
  
  const drawerWidth = 240;
  
  const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })(
    ({ theme, open }) => ({
      flexGrow: 1,
      padding: theme.spacing(3),
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      marginRight: -drawerWidth,
      ...(open && {
        transition: theme.transitions.create('margin', {
          easing: theme.transitions.easing.easeOut,
          duration: theme.transitions.duration.enteringScreen,
        }),
        marginRight: 0,
      }),
    }),
  );
  
  const AppBar = styled(MuiAppBar, {
    shouldForwardProp: (prop) => prop !== 'open',
  })(({ theme, open }) => ({
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    ...(open && {
      width: `calc(100% - ${drawerWidth}px)`,
      transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
      marginRight: drawerWidth,
    }),
  }));
  
  const DrawerHeader = styled('div')(({ theme }) => ({
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-start',
  }));
  
  
    const theme = useTheme();
    const [open, setOpen] =useState(false);
  
    const handleDrawerOpen = (e) => {
      setOpen(true);
    };
  
    const handleDrawerClose = () => {
      setOpen(false);
    };
  
  
  
  
    return (
      
      <div>
      <AppBar position="fixed" className='header' elevation={0} >

      <Container >
          <Toolbar  sx={{ padding:"0 !important",justifyContent:"space-between"}}>
            <Typography  variant="h4" className='logo'display={"flex"} href="#about" component="a" sx={{display:"flex",marginRight:"auto"}}>
         Print
            </Typography>

            <ul className='Navs'>
     
              <li className='nav-link'>
              <Link activeClass="active" to="about" spy={true} offset={0} duration={500} >
                    About
              </Link>
              </li>
              <li className='nav-link'>
              <Link activeClass="active" to="services" spy={true}  offset={-20}  duration={500} >
                    Services
              </Link>
              </li>
              <li className='nav-link'>
              <Link activeClass="active" to="blog" spy={true}   duration={500} >
                    Blog
              </Link>
              </li>
              <li className='nav-link'>
              <Link activeClass="active" to="team" spy={true} offset={-20}    duration={500} >
                    Team
              </Link>
              </li>
              <li className='nav-link'>
              <Link activeClass="active" to="contact" spy={true} offset={-60} duration={500} >
                    Contact
              </Link>
              </li>
            </ul>
            <IconButton
              onClick={(e)=>handleDrawerOpen(e)}
              sx={{ ...(open && { display: 'none' }) }}
            className='menu'
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
            >
              <SegmentIcon />
            </IconButton>
          </Toolbar>
      </Container>
  
        </AppBar>
        
         <Drawer
          sx={{
          
            width: drawerWidth,
            flexShrink: 0,
            '& .MuiDrawer-paper': {
              width: drawerWidth,
              backgroundColor:"#112240 !important",
              color:"white"
            },
          }}
          variant="persistent"
          anchor="right"
          open={open}
        >
          <DrawerHeader >
            <IconButton sx={{color:"white"}} onClick={handleDrawerClose}>
              {theme.direction === 'rtl' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
            </IconButton>
          </DrawerHeader>
          <Divider />
          <List className='MenuUl'>
            {[
            { Name:'About',
              url:'about',
              icon: <HomeIcon />
             },
            { Name:'Services',
              url:'services',
              icon: <AssignmentTurnedInIcon/>
             },
            { Name:'Blog',
              url:'blog',
              icon: <LayersIcon/>
             },
            { Name:'Team',
              url:'team',
              icon:<SettingsAccessibilityIcon />
             },
            { Name:'Contact',
              url:'contact',
              icon: <AddReactionIcon/>
             }
              ].map((text, index) => (
              
              <ListItem button key={index}>
                <Link activeClass="active" onClick={()=>handleDrawerClose()}  to={text.url} spy={true} style={{display:"flex"}} duration={500} >
                <ListItemIcon sx={{color:"white"}}>
                  {text.icon}
                </ListItemIcon>
                <ListItemText primary={text.Name} />
                {/* offset={text.url=="contact"? -20:50}  */}
                </Link>
              </ListItem>
            ))}
          </List>
        </Drawer> 
      </div>
    )
  }

export default Header;