import React from 'react'
import './style.scss'
import { Button, Container, Typography } from "@mui/material";
import EmailIcon from '@mui/icons-material/Email';
function Contact() {
  return (
    <div className='Contact' id='contact'>
      <Container>
            <div>
                  <Typography
                    variant="h2"
                    component="h2"
                    className="Section-upper-Title"
                    align="center"
                    gutterBottom
                  >
                    Contact Us
                  </Typography>
                  <Typography
                    variant="h2"
                    component="h2"
                    className="Section-bottom-Title"
                    align="center"
                    gutterBottom
                  >
                   Get In Touch
                  </Typography>
            </div>

            <div className='Contact-text'>
              <Typography variant='h4'>
                At Tech Systems
              </Typography>
              <ul className='social-links'>
                <li>
                <Button variant="text" component="a" startIcon={<EmailIcon />}> at@printatonline.tech</Button>
                </li>
              </ul>
            </div>
      </Container>
    </div>
  )
}

export default Contact