import { Container, Grid, Typography } from '@mui/material'
import React from 'react'
import { MemberCard, Staff } from '../../components'
import './style.scss'

function TeamPage() {
 
  return (
    <div className='TeamPage' id="team">
      <Container align="center">
          <div>
              <Typography variant="h2" component="h2" className='Section-upper-Title' align='center' gutterBottom>
                Our Team
              </Typography>
              <Typography variant="h2" component="h2" className='Section-bottom-Title' align='center' gutterBottom>
               Meet Our Brilliant Minds
              </Typography>
          </div>

          <Grid container rowSpacing={{xs:0,md:4}} columnSpacing={{xs:0,sm:2,md:3}}>
          {
            Staff.map((member,index)=>(
              <Grid item  xs={12} sm={6} md={3} key={index}>
                    <MemberCard member={member}/>
              </Grid>
            ))
          }
        </Grid>
      </Container>
    </div>
  )
}

export default TeamPage