import React from 'react'
import './style.scss'
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Printer from '../../assets/images/printer.png'
import Commerce from '../../assets/images/commerce.png'
import { ServiceCard } from '../../components';
function Services() {

  let services=[
    {
      name:"Printing",
      img:Printer,
      description:`No technical topic is unheard for us. 
      Our more than a hundred technical engineers enable us 
      to bring extra help to your technical challenges.
       You can learn to set up your Kubernetes applications, 
       optimise your AWS costs, or build scalable products with`
    },
    {
      name:"E-Commerce",
      img:Commerce,
      description:`No technical topic is unheard for us. 
      Our more than a hundred technical engineers enable us 
      to bring extra help to your technical challenges.
       You can learn to set up your Kubernetes applications, 
       optimise your AWS costs, or build scalable products with`
    }
  ]
  return (
    <div id='services' className='Services'>
      <Container align="center">
      <div>
          <Typography variant="h2" component="h2" className='Section-upper-Title' align='center' gutterBottom>
            Our Services
          </Typography>
          <Typography variant="h2" component="h2" className='Section-bottom-Title' align='center' gutterBottom>
            What We Have For You
          </Typography>
      </div>


      <Grid sx={{padding:"40px 0"}} container rowSpacing={{xs:4}} justifyContent={"center"}>
          {
            services.map((service,index)=>(
               <Grid item  xs={12} md={4} key={index}>
                  <ServiceCard service={service}/>
               </Grid>
            ))
          }
      </Grid>
      </Container>
    </div>
  )
}

export default Services